* {
  margin: 0;
  padding: 0;
}
body {
  margin: 0;
  padding: 0;
  font-family: Habibi;
}
.App {
  font-family: sans-serif;
  text-align: center;
}
p {
  color: blue;
  font-size: 20px;
}
a {
  text-decoration: none;
  color: #000;
}
.container {
  margin: 2%;
}
.navBar {
  background-color: #f7f7f7;
  padding: 8px 8px 8px 8px;
}
.navBar > ul {
  list-style: none;
  display: flex;
}
.navBar > ul > li {
  margin: 5px;
  padding: 4px;
  cursor: pointer;
}
.navBar > ul > li:hover {
  /* background-color:crimson; */
  color: crimson;
}
.title {
  padding: 4px;
  font-size: 18px;
}
.description {
  padding: 4px;
}
.footer {
  margin-top: 10px;
  background-color: #f7f7f7;
  padding: 5px;
  position: fixed;
  bottom: 0;
  right: 0;
  left: 0;
}
.footer > div > ul {
  list-style: none;
  display: flex;
}
.footer > div > ul > li {
  margin: 2px;
  padding: 2px;
  cursor: pointer;
}
.show {
  height: 60vh;
}
.row {
  display: flex;
}
.grid-flex {
  display: flex;
  flex-wrap: wrap;
}
.col-md-2 {
  width: 20%;
}
.col-md-3 {
  width: 30%;
}
.col-md-9 {
  width: 70%;
  padding: 10px;
}
.sidebar {
  background-color: #f7f7f7;
  padding: 5px;
}
.sidebar > ul {
  list-style: none;
}
.sidebar > ul > li {
  padding: 2px 15px 5px 15px;
  width: fit-content;
}
.sidebar > ul > li:hover {
  background-color: #ddd;
  color: #fff;
  cursor: pointer;
}
.form-group {
  margin-bottom: 5px;
}
.form-control {
  padding: 4px;
}
.btn {
  padding: 5px 10px 5px 10px;
  cursor: pointer;
}
.btn-primary {
  background-color: #f7f7f7;
  border-color: #f7f7f7;
}
.btn:hover {
  background-color: blue;
  color: #fff;
}
.card {
  padding: 10px 10px 10px 10px;
  margin: 0px 5px 0px 5px;
  background-color: #f7f7f7;
  border-radius: 4px;
}
.social-links {
  cursor: pointer;
}
.liked {
  color: red;
}
.isDisliked {
  color: blue;
}
.post {
  background: #ffffff;
  border: 1px solid #f2f2f2;
  border-radius: 3px;
  width: 100%;
  max-width: 600px;
  margin-bottom: 20px;
}
.post-flex {
  display: flex;
  background-color: #fafafa;
  padding-top: 33px;
}
.post-header {
  display: flex;
  padding: 8px 8px 8px 8px;
  border: 1px solid #f7f7f7;
  border-radius: 4px;
  background-color: #fff;
}
.post-footer {
  display: flex;
  padding: 4px 4px 4px 4px;
  border: 1px solid #f7f7f7;
  border-bottom: none;
  border-radius: 4px;
  background-color: #fff;
  margin: -4px 0px 0px -2px;
}
.post-footer > p {
  padding: 4px;
}
.action-icon {
  cursor: pointer;
  padding: 4px;
}
.profile-pic {
  padding: 6px 4px 4px 4px;
  background-color: #000;
  height: 25px;
  color: #fff;
  text-align: center;
  justify-content: center;
  width: 25px;
  border-radius: 50%;
}
.profile-name {
  padding: 4px;
  height: 25px;
  text-align: center;
  justify-content: center;
  width: 25px;
}
.left-header {
  display: flex;
  width: 50%;
}
.right-header {
  padding: 4px;
  text-align: end;
  width: 50%;
}
.col-md-1 {
  flex: 0 0 auto;
  width: 8.33333333%;
}
.col-md-2 {
  flex: 0 0 auto;
  width: 16.66666667%;
}
.col-md-3 {
  flex: 0 0 auto;
  width: 25%;
}
.col-md-4 {
  flex: 0 0 auto;
  width: 33.33333333%;
}
.col-md-5 {
  flex: 0 0 auto;
  width: 41.66666667%;
}
.col-md-6 {
  flex: 0 0 auto;
  width: 50%;
}
.col-md-7 {
  flex: 0 0 auto;
  width: 58.33333333%;
}
.col-md-8 {
  flex: 0 0 auto;
  width: 66.66666667%;
}
.col-md-9 {
  flex: 0 0 auto;
  width: 75%;
}
.col-md-10 {
  flex: 0 0 auto;
  width: 83.33333333%;
}
.col-md-11 {
  flex: 0 0 auto;
  width: 91.66666667%;
}
.col-md-12 {
  flex: 0 0 auto;
  width: 100%;
}
.post-image {
  width: -webkit-fill-available;
}
.insta-bar {
  display: flex;
  background: #fff;
  border-bottom: 1px solid #f7f7f7;
  border-left: 1px solid #f7f7f7;
  border-right: 1px solid #f7f7f7;
  padding: 12px;
}
.left-side-nav {
  text-align: end;
  width: 33.33333333%;
  margin-right: 90px;
  padding: 4px;
}
.center-nav {
  text-align: center;
  width: 33.33333333%;
}
.right-side-nav {
  display: flex;
  text-align: flex-end;
  width: 33.33333333%;
}
.nav-icon {
  padding: 4px 15px 4px 4px;
  cursor: pointer;
}
.search-bar {
  padding: 4px;
  border: 2px solid #f7f7f7;
  text-align: center;
  border-radius: 4px;
  width: 60%;
}
.right-header > div {
  display: none;
  background: #fff;
  padding: 8px;
  position: absolute;
}
.right-header > div > ul {
  list-style: none;
  padding: 1px;
}
.right-header > div > ul > li {
  border-bottom: 1px solid #f7f7ff;
  font-size: 14px;
}
.message-div {
  display: flex;
  background: #fff;
  border-bottom: 1px solid #f7f7f7;
  border-left: 1px solid #f7f7f7;
  border-right: 1px solid #f7f7f7;
  padding: 12px;
}
.message-text {
  margin: 0px 0px 0px 10px;
  display: grid;
}
.message-status {
  opacity: 0.5;
  font-size: 14px;
}
.follow-btn {
  background-color: blue;
  color: #fff;
  border: 1px solid #fff;
  border-radius: 4px;
  font-size: 14px;
  height: 30px;
  margin-left: 5px;
  padding: 8px 8px 8px 8px;
  cursor: pointer;
}
.post-comment-div {
  display: flex;
  padding: 8px;
}
.post-comment-div > input {
  padding: 2px;
  border: 1px solid #fafafa;
  width: 100%;
}
.suggestions-bar {
  margin-left: 18px;
  padding: 4px;
}
.suggestion-div {
  display: flex;
  margin-bottom: 5px;
}
.suggestion-follow-btn {
  font-size: 16px;
  padding: 4px;
  margin-left: 4px;
  cursor: pointer;
  text-transform: lowercase;
}
.suggestions-heading {
  padding: 0px 0px 10px 4px;
}
.active-now {
  height: 10px;
  width: 10px;
  border-radius: 50%;
  background: #3bad3b;
  position: fixed;
  margin-top: -10px;
}
